	module.exports = {"data":{"markdownRemark":{"html":"<style>\n.contents {\n  margin-left: 1.45rem;\n  margin-right: 1.45rem;\n  border-radius: 0.3em;\n  width: 60%;\n}\n</style>\n<p>A bounty is money you reward to hackers for reported and resolved bugs. They're used to attract the best hackers and to keep them incentivized to hack your programs. You can use bounties to encourage hackers to focus on particular assets by altering the reward amount for different vulnerability types. You shouldn't feel obligated to award a bounty for every incoming report as it's best to only reward for useful, valid reports.</p>\n<div class=\"background contents\" markdown=\"1\">\n<table>\n<thead>\n<tr>\n<th>In this article, you can learn about:</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><a href=\"#awarding\">Awarding Bounties on Reports</a></td>\n</tr>\n<tr>\n<td><a href=\"#suggesting\">Suggesting Bounties</a></td>\n</tr>\n<tr>\n<td><a href=\"#best\">Best Practices</a></td>\n</tr>\n<tr>\n<td><a href=\"#bounties\">Bounties for Reports Received Outside of HackerOne</a></td>\n</tr>\n</tbody>\n</table>\n</div>\n<h3 id=\"awarding\">Awarding Bounties on Reports</h3>\n<p>You can award a bounty through any report submitted to HackerOne. Some teams prefer to award a bounty once the issue has been confirmed as valid, while others wait until the issue is resolved.</p>\n<p>To award a bounty:</p>\n<ol>\n<li>Go to your inbox and open the report you'd like to award a bounty for.</li>\n<li>Expand the action picker at the bottom of the report above the comment box.</li>\n<li>Select <strong>Set award</strong>.</li>\n<li>Enter the bounty amount you wish to award. You can select one of the pre-calculated median, competitive, and top bounty awards for severity level of the bug.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/bountystats-be498dba2f243a4e586256966a1eaa52-27e59.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 28.57142857142857%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"awarding bounties\"\n        title=\"\"\n        src=\"/static/bountystats-be498dba2f243a4e586256966a1eaa52-2f183.png\"\n        srcset=\"/static/bountystats-be498dba2f243a4e586256966a1eaa52-67829.png 125w,\n/static/bountystats-be498dba2f243a4e586256966a1eaa52-5c59f.png 250w,\n/static/bountystats-be498dba2f243a4e586256966a1eaa52-2f183.png 500w,\n/static/bountystats-be498dba2f243a4e586256966a1eaa52-27e59.png 707w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Bounty amounts can be increased at any point by setting another award on the report, but keep in mind that bounties can't be removed once awarded.</p>\n<h3 id=\"suggesting\">Suggesting Bounties</h3>\n<p>If you're unsure of how much to award the hacker, you can communicate a suggested amount with your internal team. To suggest an amount:</p>\n<ol>\n<li>Go to your inbox and open the report you'd like to award a bounty for.</li>\n<li>Expand the action picker at the bottom of the report above the comment box.</li>\n<li>Select <strong>Set award</strong>.</li>\n<li>Select <strong>Suggest amount</strong> in the next action picker.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-b92a9.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 39.285714285714285%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"suggesting bounties\"\n        title=\"\"\n        src=\"/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-2f183.png\"\n        srcset=\"/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-67829.png 125w,\n/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-5c59f.png 250w,\n/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-2f183.png 500w,\n/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-c93e8.png 750w,\n/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-4e628.png 1000w,\n/static/bounties-suggest-e9775d4447d189abf691d97a8ee0a925-b92a9.png 1400w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"5\">\n<li>Enter your suggested bounty amount.</li>\n<li><i>(Optional)</i> Enter your reason of why you suggest that bounty amount.</li>\n<li>Click <strong>Suggest award</strong>.</li>\n</ol>\n<h3 id=\"best\">Best Practices</h3>\n<p>Here are some best practices to follow when awarding bounties:</p>\n<ul>\n<li>Provide bounties for useful, valid reports.</li>\n<li>Award a bounty for a significant found vulnerability that is out of scope.</li>\n<li>Clearly communicate to hackers your reasons for awarding or declining a bounty.</li>\n</ul>\n<blockquote>\n<p><i>Note: Professional, Enterprise, and Fully Managed programs have access to a HackerOne representative who can provide insight and consult them through the bounty awarding process.</i></p>\n</blockquote>\n<h3 id=\"bounties\">Bounties for Reports Received Outside of HackerOne</h3>\n<p>When hackers submit vulnerabilities to your organization outside of HackerOne, you can leverage the HackerOne API to award hackers for their efforts. To start paying hackers, generate an API token on your Program settings page. Keep in mind that this API endpoint is not for awarding bounties for reports on HackerOne itself, but only for reports that were reported outside of HackerOne.</p>\n<blockquote>\n<p><i>Note: This option is only available for HackerOne Professional, Enterprise, and Community editions.</i></p>\n</blockquote>\n<p>To start paying hackers:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Automation > API</strong>.</li>\n<li>Click <strong>Create API Token</strong> to create an API token.</li>\n<li>Enter the unique identifier to authenticate the token in the <strong>New API Token</strong> window.</li>\n<li>Click <strong>Create</strong>.</li>\n<li>The API token will be generated and presented to you. This is the only time the API token is shown to you.</li>\n<li>Click <strong>Manage groups</strong> next to the API identifier to grant reward permissions to various groups.</li>\n<li>Select the groups you want to give reward permissions to. By default, the Standard group has Reward permissions.</li>\n<li>Make sure your billing methods have been complete so that there's a way to pay out bounties in <strong>Settings > General > Billing</strong>.</li>\n<li>Configure how to award a bounty. Go to <a href=\"https://api.hackerone.com/docs/v1#/programs/bounties\">this page</a> to see the documentation on how to award a bounty. See the code example in cURL and Ruby that'll help you. <i>Note: the 1337 program ID used in the example below is not real and should be replaced with your own program ID. You can find your program ID in report objects or by asking your HackerOne program manager.</i></li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>cURL</th>\n<th>Ruby</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-e2a83.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 65.06849315068493%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"cURL\"\n        title=\"\"\n        src=\"/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-2f183.png\"\n        srcset=\"/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-67829.png 125w,\n/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-5c59f.png 250w,\n/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-2f183.png 500w,\n/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-c93e8.png 750w,\n/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-4e628.png 1000w,\n/static/bountiesforreportsoutsideofh1-1-3056545b221444695120a7efb9234060-e2a83.png 1168w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </td>\n<td>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-82622.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 98.17518248175183%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Ruby\"\n        title=\"\"\n        src=\"/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-2f183.png\"\n        srcset=\"/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-67829.png 125w,\n/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-5c59f.png 250w,\n/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-2f183.png 500w,\n/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-c93e8.png 750w,\n/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-4e628.png 1000w,\n/static/bountiesforreportsoutsideofh1-2-89cdb339649beb3e3ba9c62ba6b3b928-82622.png 1096w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </td>\n</tr>\n</tbody>\n</table>\n<ol start=\"10\">\n<li>Award the bounty for the hacker.</li>\n</ol>\n<p>After the bounty has been awarded, the hacker will receive an email to claim the bounty. HackerOne will collect the person's tax form before processing the payout. The awarded amount, including your applicable fees, will be deducted from your balance immediately. A resolved dummy report will show up in your Bugs overview, which will help you keep track of the bounties you've paid out.</p>\n<p>For technical questions or help with your implementation, please reach out to support@hackerone.com or your HackerOne program manager.</p>","frontmatter":{"path":"/programs/bounties.html","id":"programs/bounties","title":"Bounties","description":null}}},"pathContext":{}}

